const storage = window.localStorage;
const storageKey = 'HELLO_FRESH_GUPTAADITYA_ASSIGNMENT';

export function loadState() {
  const state = storage.getItem(storageKey);
  if (!state) return {};
  try {
    return JSON.parse(state);
  } catch (e) {
    console.log('Corrupt State');
    return {};
  }
}

export function saveState(state = {}) {
  let saveStateAsString = '';
  try {
    saveStateAsString = JSON.stringify(state);
  } catch (e) {
    console.log('Corrupt State');
    saveStateAsString = '';
  }
  return storage.setItem(storageKey, saveStateAsString);
}

export const sanitizeRating = rating => Math.floor(Number(rating) || 0);
