import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import RecipeCard from 'components/RecipeCard';
import * as queries from './queries';

export default class RecipesList extends React.Component {
  componentDidMount() {
    this.props.onGetRecipesList();
  }

  render() {
    const { recipes, onRatingChange } = this.props;
    return (
      <div className="recipes-row">
        {recipes.map((recipe, index) => {
          const recipeSlug = queries.createSlug(recipe.name);
          return (
            <Link
              key={index}
              to={{
                pathname: `/hfd/recipe/${recipeSlug}`,
                state: { recipe },
              }}>
              <RecipeCard recipe={recipe} onRatingChange={onRatingChange} />
            </Link>
          );
        })}
      </div>
    );
  }
}
RecipesList.propTypes = {
  recipes: PropTypes.arrayOf(PropTypes.object),
  onGetRecipesList: PropTypes.func.isRequired,
  onRatingChange: PropTypes.func.isRequired,
};
RecipesList.defaultProps = {
  recipes: [],
};
