import { connect } from 'react-redux';
import RecipesList from './recipesList';
import * as actions from './actions';

function mapStateToProps(state) {
  return {
    recipes: state.recipesList.recipes,
  };
}
function mapDispatchToProps(dispatch) {
  const dispatchBoundFunction = func => {
    return (...params) => dispatch(func(...params));
  };

  return {
    onGetRecipesList: dispatchBoundFunction(actions.onGetRecipes),
    onRatingChange: dispatchBoundFunction(actions.onRecipeRatingChange),
  };
}
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(RecipesList);
