import * as actionTypes from './actionTypes';

export function onGetRecipes() {
  return {
    type: actionTypes.ON_GET_RECIPES,
  };
}

export function onGetRecipesSuccess(recipes) {
  return {
    type: actionTypes.ON_GET_RECIPES_SUCCESS,
    recipes,
  };
}

export function onRecipeRatingChange(rating, recipeId) {
  return {
    type: actionTypes.ON_CHANGE_RECIPE_RATING,
    rating,
    recipeId,
  };
}
