import { takeLatest, put } from 'redux-tale/es/effects';
import * as actions from './actions';
import * as actionTypes from './actionTypes';
import { url } from './constants';

function* onGetRecipes() {
  try {
    const recipes = yield fetch(url).then(response => response.json());
    yield put(actions.onGetRecipesSuccess(recipes));
  } catch (error) {
    alert('It seems there is some issue with JSON fetching');
  }
}

const onGetRecipesSaga = takeLatest(actionTypes.ON_GET_RECIPES, onGetRecipes);

export default [onGetRecipesSaga];
