import React from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import Ratings from 'blocks/Ratings';
import { sanitizeRating } from 'utils/helper';

class RecipeDetails extends React.Component {
  static getDerivedStateFromProps(props) {
    const {
      location: { state: { recipe: { id: recipeId = '' } = {} } = {} } = {},
    } = props;
    return {
      recipe: props.recipes.find(r => r.id === recipeId),
    };
  }

  constructor() {
    super();
    this.state = {
      recipe: null,
    };
    this.handleRatingChange = this.handleRatingChange.bind(this);
  }

  handleRatingChange(rating) {
    const { onRatingChange } = this.props;
    const { recipe } = this.state;
    onRatingChange(sanitizeRating(rating), recipe.id);
  }

  render() {
    const { recipe } = this.state;
    if (!recipe) return 'No such recipe found';
    const { history } = this.props;
    return (
      <div className="recipeDetails">
        <div
          className="header"
          style={{ backgroundImage: `url(${recipe.image})` }}
        />
        <div className="content">
          <div className="text">
            <div className="row">
              <div className="cell">
                <h1 className="food">{recipe.name}</h1>
                <div className="info">{recipe.headline}</div>
              </div>
              <div className="cell">
                <div className="row">
                  <div className="cell">
                    <div className="nutritionalFact">
                      <h3>Nutritional Fact</h3>
                      <ul>
                        <li>
                          Carbs:{' '}
                          <data value={recipe.carbos}>{recipe.carbos}</data>
                        </li>
                        <li>
                          Fats: <data value={recipe.fats}>{recipe.fats}</data>
                        </li>
                        <li>
                          Protein:{' '}
                          <data value={recipe.proteins}>{recipe.proteins}</data>
                        </li>
                      </ul>
                    </div>
                  </div>
                  <div className="cell" style={{ flex: 2 }}>
                    <i className="fa fa-clock-o">
                      {' '}
                      {recipe.time.substr(2, 2)} Min
                    </i>
                    <i className="fa fa-fire"> {recipe.calories}</i>
                    <i className="fa fa-heart">
                      <span className="badge"> ({recipe.favorites})</span>
                    </i>
                    <div className="ratings">
                      <Ratings
                        rating={sanitizeRating(recipe.rating)}
                        onRatingChange={this.handleRatingChange}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="cell">
                <div className="ingredients">
                  <h3>Ingredients: </h3>
                  <ol>
                    {recipe.ingredients.map((ingredient, index) => (
                      <li key={index}>{ingredient}</li>
                    ))}
                  </ol>
                </div>
              </div>
              <div className="cell">
                <div className="description">{recipe.description}</div>
              </div>
            </div>
          </div>
        </div>
        <div className="footer">
          <button onClick={() => history.push('/hfd/recipes')}>Back</button>
        </div>
      </div>
    );
  }
}

RecipeDetails.propTypes = {
  location: PropTypes.object.isRequired,
  recipes: PropTypes.array.isRequired,
  onRatingChange: PropTypes.func.isRequired,
};

export default withRouter(RecipeDetails);
