import React from 'react';
import PropTypes from 'prop-types';
import Ratings from '../../blocks/Ratings';
import { sanitizeRating } from 'utils/helper';

export default class RecipeCard extends React.Component {
  constructor() {
    super();
    this.handleRatingChange = this.handleRatingChange.bind(this);
  }

  handleRatingChange(rating) {
    const { onRatingChange, recipe } = this.props;
    onRatingChange(sanitizeRating(rating), recipe.id);
  }

  render() {
    const { recipe } = this.props;
    if (!recipe) return null;
    return (
      <div className="card">
        <div
          className="header"
          style={{ background: `url(${recipe.image}) no-repeat center` }}
        />
        <div className="content">
          <div className="text">
            <h3 className="food">{recipe.name}</h3>
            <p className="info">{recipe.headline}</p>
            <i className="fa fa-clock-o"> {recipe.time.substr(2, 2)} Min</i>
            <i className="fa fa-fire"> {recipe.calories}</i>
          </div>
          <div className="ratings">
            <Ratings
              rating={sanitizeRating(recipe.rating)}
              onRatingChange={this.handleRatingChange}
            />
          </div>
        </div>
      </div>
    );
  }
}
RecipeCard.propTypes = {
  recipe: PropTypes.object.isRequired,
  onRatingChange: PropTypes.func,
};
RecipeCard.defaultProps = {
  recipe: null,
};
