import React from 'react';
import PropTypes from 'prop-types';

export default class Star extends React.Component {
  constructor() {
    super();
    this.handleStarTap = this.handleStarTap.bind(this);
  }

  handleStarTap(e) {
    const { rate, onStarTap } = this.props;
    onStarTap(rate);
    e.preventDefault();
  }

  render() {
    const { filled } = this.props;
    let className = 'fa fa-star-o';
    if (filled) {
      className = 'fa fa-star';
    }
    return (
      <a href="#" onClick={this.handleStarTap}>
        <i className={className} />
      </a>
    );
  }
}
Star.propTypes = {
  onStarTap: PropTypes.func.isRequired,
  rate: PropTypes.number.isRequired,
  filled: PropTypes.bool.isRequired,
};
Star.defaultProps = {
  onStarTap: PropTypes.func.isRequired,
  filled: false,
};
