import * as actionTypes from './actionTypes';

const initialState = {
  recipes: [],
};

export default function recipesList(state = initialState, action) {
  switch (action.type) {
    case actionTypes.ON_GET_RECIPES_SUCCESS:
      return Object.assign({}, state, { recipes: action.recipes });
    case actionTypes.ON_CHANGE_RECIPE_RATING:
      return {
        ...state,
        recipes: state.recipes.map(r => {
          if (r.id !== action.recipeId) return r;
          return {
            ...r,
            rating: action.rating,
          };
        }),
      };
    default:
      return state;
  }
}
