import React from 'react';
import PropTypes from 'prop-types';
import Star from './Star';

export default function Ratings(props) {
  const { rating, onRatingChange } = props;
  const filledStars = new Array(5).fill(false);
  filledStars.fill(true, 0, rating);
  return (
    <div className="stars">
      <li>
        {filledStars.map((filled, index) => {
          return (
            <Star
              key={index}
              rate={Number(index + 1)}
              filled={filled}
              onStarTap={onRatingChange}
            />
          );
        })}
      </li>
    </div>
  );
}
Ratings.propTypes = {
  rating: PropTypes.number.isRequired,
  onRatingChange: PropTypes.func.isRequired,
};
